import React, {Component} from 'react';
import './index.css';
import { Redirect } from 'react-router-dom';
import { FacebookProvider, Share, Comments, CommentsCount } from 'react-facebook';

class LandingPageComponent extends Component {
    state = { loggedIn: false, pageData: null };
    componentWillMount() {
        const token = localStorage.getItem('token');
        if (token && token !== null && token !== "") {
            this.setState({loggedIn: true});
        }
    }
    logout(){
        localStorage.removeItem('token');
        this.setState({ loggedIn: false });
    }
    render() {
        if (!this.state.loggedIn){
            return <Redirect to="/login" />;
        }
        return (
            <div className="landing-page-wrapper">
                <h1>Welcome to AIQ TEST APP</h1>
                <span className="logout-btn" onClick={this.logout.bind(this)}>Logout</span>
            </div>
        )
    }
}

export default LandingPageComponent;


